<template>
    <div class="goods">
        <div class="menu-wrapper" v-el:menu-wrapper>
            <ul >
                <li v-for = "item in goods" class="menu-item" :class="{'current':currentIndex===$index}" @click="selectMenu($index,$event)">
                    <span class="text border-1px">
                        <span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name}}
                    </span>
                </li >
            </ul >
        </div>

        <div class="foods-wrapper" v-el:foods-wrapper>
            <ul class="foods-wrapper-box">
                <li v-for="item in goods" class="food-list food-list-hook">
                    <h1 class="title">{{item.name}}</h1>
                    <ul >
                        <li v-for = "food in item.foods" class = "food-item border-1px" @click="selectFood(food,$event)">
                            <div class="icon">
                                <img :src = "food.image" alt = "食物图片" width="57" height="57" />
                            </div >
                            <div class="content">
                                <h2 class="name">{{food.name}}</h2>
                                <p class="desc">{{food.description}}</p>
                                <div class="extra">
                                    <span class="count">月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span>
                                </div>
                                <div class="price">
                                    <span class="now">￥{{food.price}}</span><span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                                </div>
                                <div class="cartcontrol-wrapper">
                                    <cartcontrol :food="food"></cartcontrol>
                                </div>
                            </div >
                        </li >
                    </ul >
                </li>
            </ul>

        </div>

        <Shopcart v-ref:shopcart :select-foods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></Shopcart>
        <Food :food="selectedFood" v-ref:food></Food>
    </div>
</template>

<script>
    import BScroll from 'better-scroll'
    import Shopcart from '../shopcart/shopcart'
    import Cartcontrol from '../cartcontrol/cartcontrol'
    import Food from '../food/food'
    // const ERR_OK = 0
    import Data from '../../../data.json'
    export default {
        props: {
            seller: {
                type: Object
            }
        },
        data() {
            return {
                goods: [],
                listHeight: [],
                scrollY: 0,
                selectedFood: {}
            }
        },
        computed: {
            currentIndex() {
                for (let i = 0; i < this.listHeight.length; i++) {
                    let height1 = this.listHeight[i]
                    let height2 = this.listHeight[i + 1]
                    if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
                        return i
                    }
                }
                return 0
            },
            selectFoods() { // 监听cartcontrol组建的 选择的food数据变化会传递到goods变化然后继续传递到Shopcart 从而实现购物车数量变化
                let foods = []
                this.goods.forEach((good) => {
                    good.foods.forEach((food) => {
                        if (food.count) {
                            foods.push(food)
                        }
                    })
                })
                return foods
            }
        },
        created() {
            this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
           /* this.$http.get('/api/goods').then((response) => {
                response = response.body
            if (response.errno === ERR_OK) {
                    this.goods = response.data
                    this.$nextTick(() => {
                        this._initScroll()
                        this._calculateHeight()
                    })
                }
            })*/
            this.goods = Data.goods
            this.$nextTick(() => {
                this._initScroll()
                this._calculateHeight()
            })
        },
        methods: {
            selectFood(food, event) {
                if (!event._constructed) {
                    return
                }
                this.selectedFood = food
                // 调用子组件的自定义show方法
                this.$refs.food.show()
            },
            _drop(target) { // this.$refs.shopcart访问子组件shopcart HTML里写法是：v-ref:shopcart;  this.$refs.shopcart.drop(target) 是将当前选中的target 传递给子组件并调用子组件methods中的drop方法
                this.$nextTick(() => {
                    // 体验优化，异步执行drop动画
                    this.$refs.shopcart.drop(target)
                })
            },
            _initScroll() {
                this.menuScroll = new BScroll(this.$els.menuWrapper, {
                    click: true
                })
                this.foodsScroll = new BScroll(this.$els.foodsWrapper, {
                    click: true,
                    probeType: 3
                })

                this.foodsScroll.on('scroll', (pos) => {
                    this.scrollY = Math.abs(Math.round(pos.y))
                })
            },
            _calculateHeight() {
                let foodList = this.$els.foodsWrapper.getElementsByClassName('food-list-hook')
                let height = 0
                this.listHeight.push(height)
                for (let i = 0; i < foodList.length; i++) {
                    let item = foodList[i]
                    height += item.clientHeight
                    this.listHeight.push(height)
                }
            },
            selectMenu(index, event) {
                if (!event._constructed) {  // 如果是pc 端的原生点击事件 则忽略掉避免点击一次出现俩次运行函数情况 自定义的才生成有效执行
                    return
                }
                let foodlist = this.$els.foodsWrapper.getElementsByClassName('food-list-hook')
                let el = foodlist[index]
                this.foodsScroll.scrollToElement(el, 300)
                console.log(index)
            }
        },
        components: {
            Shopcart,
            Cartcontrol,
            Food
        },
        events: {
            'cart.add'(target) {
                this._drop(target)
            }
        }
    }
</script>

<style lang="stylus" rel="stylesheet/stylus">
    @import "../../common/stylus/mixin.styl"

    .goods
        display: flex
        position: absolute
        top: 174px
        bottom: 46px
        width: 100%
        overflow: hidden
        .menu-wrapper
            flex: 0 0 80px
            width: 80px
            background:#f3f5f7
            .menu-item
                display: table
                height: 54px
                line-height:14px
                width: 56px
                padding: 0 12px
                font-size:12px
                &.current
                    position: relative
                    z-index: 10
                    margin-top: -1px
                    background: purple
                    font-weight: 700
                    .text
                        border: none
                        color: #fff
                .icon
                    display: inline-block
                    vertical-align: stop
                    width: 12px
                    height: 12px
                    margin-right: 2px
                    background-size: 12px 12px
                    background-repeat: no-repeat
                    &.decrease
                        bg-image('decrease_3')
                    &.discount
                        bg-image('discount_3')
                    &.guarantee
                        bg-image('guarantee_3')
                    &.invoice
                        bg-image('invoice_3')
                    &.special
                        bg-image('special_3')
                .text
                    display: table-cell
                    width: 56px
                    vertical-align: middle
                    border-1px(rgba(7, 17, 27, 0.1))
                    font-size: 12px
        .foods-wrapper
            flex: 1
            .title
                padding-left: 14px
                height: 26px
                line-height: 26px
                border-left: 2px solid #d9dde1
                font-size: 12px
                color: rgb(147,153,159)
                background: #f3f5f7
            .food-item
                display: flex
                margin: 18px
                padding-bottom: 18px
                border-1px(rgba(7, 17, 27, 0.1))
                &:last-child
                    border-none()
                    margin-bottom: 0
                .icon
                    /*这个表示始终占57px大小 不缩放*/
                    flex: 0 0 57px
                    margin-right: 10px
                .content
                    /*自动站满剩余空间*/
                    flex: 1
                    .name
                        margin: 2px 0 8px 0
                        height: 14px
                        line-height: 14px
                        font-size: 14px
                        color: rgb(7,17,27)
                    .desc, .extra
                        line-height: 10px
                        font-size: 10px
                        color: rgb(147,153,159)
                    .desc
                        line-height:12px
                        margin-bottom: 8px
                    .extra
                        margin-bottom: 4px
                        line-height: 10px
                        &>count
                            margin-right: 12px
                    .price
                        font-weight: 700
                        line-height: 14px
                        .now
                            margin-right: 8px
                            font-size: 14px
                            color: rgb(240,20,20)
                            vertical-align: top
                        .old
                            text-decoration: line-through
                            font-size: 10px
                            color: rbg(147,153,159)
                            vertical-align: top
                    .cartcontrol-wrapper
                        position: absolute
                        right: 0
                        bottom: 12px


</style>



