<template>
  <div class="app">


    <v一二header :seller="seller"></v一二header>
    <div class="tab">
        <div class="tab-item">
            <a v-link="{path:'/goods'}">商品</a>
        </div>
        <div class="tab-item">
            <a v-link="{path:'/ratings'}">评论</a>
        </div>
        <div class="tab-item">
            <a v-link="{path:'/seller'}">商家</a>
        </div>
    </div>
    <!--路由外链-->
    <router-view :seller="seller"></router-view>

    <p style="width:100%;height:46px;line-height:46px;font-size:24px;text-align:center;position: absolute;bottom: 0;left: 0;text-align: center;color:purple;background: gray;">
      Welcome to Vue.js app_1.x!
    </p>

  </div>
</template>

<script>
import Hello from './components/Hello'
import Header from './components/header/header'
import Goods from './components/goods/goods'
import Ratings from './components/ratings/ratings'
import Seller from './components/seller/seller'
import Data from '../data.json'
// const ERR_OK = 0

export default {
  data() {
    return {
        seller: {}
    }
  },
  // 类似ajax的vue-resource使用方法
  created() {
  /*  this.$http.get('/api/seller').then((response) => {
      response = response.body
        if (response.errno === ERR_OK) {
            this.seller = response.data
        }
    })  */
    this.seller = Data.seller
  },
  components: {
    Hello,
    v一二header: Header,
    Goods,
    Ratings,
    Seller
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
    @import "./common/stylus/mixin.styl"
html {
  height: 100%;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.app
    width:100%
    min-height:100%
    .tab
        display: flex
        width: 100%
        height:40px
        line-height: 40px
        .tab-item
            font-size: 20px
            text-align: center
            flex: 1
            &>a
                display: block
                color: purple
                &.active
                    color: red
</style>



