<template >
    <div class="cartcontrol">
        <div class = "cart-descrease" v-show="food.count>0" @click.stop.prevent="decreaseCart($event)" transition="move">
            <span class="inner"></span>
        </div >
        <div class = "cart-count" v-show="food.count>0">{{food.count}}</div >
        <div class = "cart-add" @click.stop.prevent="addCart($event)"></div >
    </div>
</template >

<script >
    import Vue from 'vue'
    export default {
        props: {
            food: {
                type: Object
            }
        },
        data() {
            return {}
        },
        created() {},
        methods: {
            addCart(event) {
                // console.log('进来了addCart')   //  还是得 阻止非手机端dom click的派生事件 防止二次调用
                if (!event._constructed) {
                    return
                }
                if (!this.food.count) {
                    //  this.food.count = 1 是不会生效的 因为vue在处理一个对象不存在的属性直接赋值时候是不会触发 监听改变事件
                    Vue.set(this.food, 'count', 1)
                    // console.log('点击了!addCart')
                } else {
                    this.food.count++
                    // console.log('点击了addCart')
                }
                this.$dispatch('cart.add', event.target)
            },
            decreaseCart(event) {
                if (!event._constructed) {
                    return
                }
                if (this.food.count) {
                    this.food.count--
                }
            }
        }
    }
</script >

<style lang = "stylus" rel = "stylesheet/stylus" >
@import "../../common/stylus/mixin.styl"
.cartcontrol
    font-size: 0
    .cart-descrease
        display: inline-block
        transition: all 0.4s linear
        .inner
            bg-image('cartDescrease')
            background-size: 100% 100%
            display: inline-block
            width: 18px
            height: 18px
            color: rgb(0,160,220)
        &.move-transition
            opacity: 1
            transform: translate3d(0,0,0)
            .inner
                transition: all 0.4s linear
                transform: rotate(0)
        &.move-enter, &.move-leave
            opacity: 0
            transform: translate3d(24px, 0, 0)
            .inner
                transform: rotate(180deg)
    .cart-count
        display: inline-block
        line-height: 24px
        width:12px
        height: 16px
        transform: translateY(-4px)
        padding-left: 12px
        padding-right: 12px
        text-align: center
        font-size: 14px
        color: rgb(147,153,159)
    .cart-add
        display: inline-block
        bg-image('cartAdd')
        background-size: 100% 100%
        line-height: 18px
        margin: 6px
        margin-bottom: 0
        width: 18px
        height: 18px
        color: rgb(0,160,220)
</style >



