import Vue from 'vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import App from './App'
import Goods from './components/goods/goods'
import Ratings from './components/ratings/ratings'
// components直接写也可以 以为wenbpack里有一项 别名设置成了绝对路径下的src/components 所有写成相对路径的components都可以直接这么写
import Seller from 'components/seller/seller'

// 原本是import './common/stylus/index.styl'但是由于我们在webpack配置里照着components做了增加以下common简称路径设定所以可以直接用
// 'components': path.resolve(__dirname, '../src/components'),'common': path.resolve(__dirname, '../src/common')
import 'common/stylus/index.styl'

Vue.use(VueRouter)
Vue.use(VueResource)

let app = Vue.extend(App)

let router = new VueRouter({
  linkActiveClass: 'active'
})

router.map({
  '/goods': {
    component: Goods
  },
  '/ratings': {
    component: Ratings
  },
  '/seller': {
    component: Seller
  }
})

router.start(app, '#app')

// 任何一行js块状代码（除了注释之外）输完都回车多敲一行预留空白 否则报错 router.go方法直接跳到一个路径地址
 router.go('/goods')



