<template >
    <div v-show="showFlag" class="food" transition="move" v-el:food>
        <div class = "food-content" >
            <div class = "image-header" >
                <img alt = "" :src = "food.image" />
                <div class="back" @click="hide">
                    <i class="icon-arrow_lift">&lt;</i>
                </div>
            </div >
            <div class = "content" >
                <h1 class = "title">{{food.name}}</h1>
                <div class = "food-detail">
                    <span class = "sell-count" >月售{{food.sellCount}}份</span >
                    <span class="rating">好评率{{food.rating}}%</span>
                </div >
                <div class = "price" >
                    <span class = "now" >￥{{food.price}}</span >
                    <span class = "old" v-show="food.oldPrice">￥{{food.oldPrice}}</span >
                </div >
                <div class="cartcontrol-wrapper">
                    <Cartcontrol :food="food"></Cartcontrol>
                </div>
                <div class="buy" @click.stop.prevent="addfirst(food,$event)" v-show="!food.count||food.count===0" transition="fade">加入购物车</div>
            </div >
            <!--商品信息-->
            <Split v-show="food.info"></Split>
            <div class="info" v-show="food.info">
                <h1 class="title">商品信息</h1>
                <p class="text">{{food.info}}</p>
            </div>
            <!--商品评价-->
            <Split></Split>
            <div class="rating">
                <h1 class="title">商品评价</h1>
                <Ratingselect :select-type="selectType" :only-content:="onlyContent" :desc="desc" :ratings="food.ratings"></Ratingselect>

                <div class="rating-wrapper">
                    <ul v-show="food.ratings && food.ratings.length">
                        <li v-for="rating in food.ratings" class="rating-item border-1px" v-show="needShow(rating.rateType,rating.text)">
                            <div class = "user" >
                                <span class = "name" >{{rating.username}}</span >
                                <img  alt = "头像"
                                      class = "avatar"
                                      width = "12"
                                      height = "12"
                                      :src = "rating.avatar" />
                            </div >
                            <div class="time">{{rating.rateTime | formatDate}}</div>
                            <p class="text">
                                <span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}">√</span>
                                <span class="text-content">{{rating.text}}</span>
                            </p>
                        </li>
                    </ul>
                    <div class="no-rating" v-show="!food.ratings || !food.ratings.length">暂无评价</div>
                </div>
            </div>
        </div >
    </div>
</template >

<script >
    import BScroll from 'better-scroll'
    import Vue from 'vue'
    import {formatDate} from 'common/js/date'
    import Cartcontrol from '../cartcontrol/cartcontrol'
    import Split from '../split/split'
    import Ratingselect from 'components/ratingselect/ratingselect'
    const POSITIVE = 0
    const NEGATIVE = 1
    const ALL = 2
    console.log(POSITIVE + NEGATIVE)
    export default {
        props: {
            food: {
                type: Object
            }
        },
        data() {
            return {
                showFlag: false,
                selectType: ALL,
                onlyContent: false,
                desc: {
                    all: '全部',
                    positive: '推荐',
                    negative: '吐槽'
                }
            }
        },
        methods: {
            show() {
                this.showFlag = true
                this.selectType = this.selectType
                this.onlyContent = this.onlyContent
                this.$nextTick(() => {
                    if (!this.scroll) {
                        this.scroll = new BScroll(this.$els.food, {
                            click: true
                        })
                    } else {
                        this.scroll.refresh()
                    }
                })
            },
            hide() {
                this.showFlag = false
            },
            addfirst(food, event) {
                console.log('click')
                if (!event._constructed) {
                    console.log('click原生')
                    return
                }
                console.log(event.target)
                this.$dispatch('cart.add', event.target)
                Vue.set(food, 'count', 1)
            },
            needShow(type, text) {
                if (this.onlyContent && !text) {
                    return false
                }
                if (this.selectType === ALL) {
                    return true
                } else {
                    return type === this.selectType
                }
            }
        },
        components: {
            Cartcontrol,
            Split,
            Ratingselect
        },
        events: {
            'ratingtype.select'(type) {
                this.selectType = type
                this.$nextTick(() => {
                    this.scroll.refresh()
                })
            },
            'content.toggle'(onlyContent) {
                this.onlyContent = onlyContent
                this.$nextTick(() => {
                    this.scroll.refresh()
                })
            }
        },
        filters: {
            formatDate(time) {
                let date = new Date(time)
                return formatDate(date, 'yyyy-MM-dd hh:mm')
            }
        }
    }
</script >

<style lang = "stylus" rel = "stylesheet/stylus" >
    @import "../../common/stylus/mixin.styl"
    .food
        position: fixed
        bottom: 48px
        left: 0
        top: 0
        z-index: 30
        width: 100%
        background: #fff
        &.move-transition
            transition: all 0.2s linear
            transform: translate3d(0,0,0)
        &.move-enter,&.move-leave
            transform: translate3d(100%,0,0)
        .image-header
            position: relative
            width: 100%
            height: 0
            padding-bottom: 100%
            img
                position: absolute
                top: 0
                left: 0
                width: 100%
                height: 100%
            .back
                position: absolute
                top: 10px
                left: 0
                .icon-arrow_lift
                    display: block
                    padding: 10px
                    font-size: 20px
                    color: #fff
        .content
            position: relative
            padding: 18px
            .title
                line-height: 14px
                margin-bottom: 8px
                font-size: 14px
                font-weight: 700
                color: rgb(7,17,27)
            .food-detail
                margin-bottom: 18px
                height: 10px
                line-height: 10px
                font-size: 0
                .sell-count,.rating
                    color: rgb(147,153,159)
                    font-size: 10px
                .sell-count
                    margin-right: 12px
            .price
                font-weight: 700
                line-height: 24px
                .now
                    margin-right: 8px
                    font-size: 14px
                    color: rgb(240,20,20)
                    vertical-align: top
                .old
                    text-decoration: line-through
                    font-size: 10px
                    color: rbg(147,153,159)
                    vertical-align: top
            .cartcontrol-wrapper
                position: absolute
                right: 12px
                bottom: 18px
            .buy
                position: absolute
                right: 16px
                bottom: 18px
                z-index: 10
                height: 24px
                line-height: 24px
                padding: 0 12px
                box-sizing: border-box
                font-size: 10px
                border-radius: 12px
                color: #ffffff
                background: rgb(0,160,220)
                &.fade-transition
                    transition: all 0.2s
                    opacity: 1
                &.fade-enter,&.fade-leave
                    opacity: 0
        .info
            padding: 18px
            .title
                line-height: 14px
                margin-bottom: 6px
                font-size: 14px
                color: rgb(7,17,27)
            .text
                padding: 0 8px
                line-height: 24px
                font-size: 12px
                color: rgb(77,85,93)
        .rating
            padding-top: 18px
            .title
                line-height: 14px
                padding-left: 18px
                font-size: 14px
                color: rgb(7,17,27)
            .rating-wrapper
                padding: 0 18px
                .rating-item
                    position: relative
                    padding: 16px 0
                    border-1px(rgba(7,17,27,0.1))
                    .user
                        position: absolute
                        top: 16px
                        right: 0
                        line-height: 12px
                        font-size: 0
                        .name
                            display: inline-block
                            margin-right: 6px
                            vertical-align: top
                            font-size: 10px
                            color:rgb(147,153,159)
                        .avatar
                            display: inline-block
                            border-radius: 50%
                    .time
                        margin-bottom: 6px
                        line-height: 12px
                        font-size: 10px
                        color:rgb(147,153,159)
                    .text
                        line-height: 16px
                        font-size: 12px
                        color: rgb(7,17,27)
                        .icon-thumb_down,.icon-thumb_up
                            display: inline-block
                            width: 14px
                            height: 14px
                            vertical-align: middle
                            margin-right: 4px
                            line-height: 14px
                            font-size: 12px
                            text-align: center
                        .icon-thumb_up
                            color: red
                        .icon-thumb_down
                            color: blue
                        .text-content
                            display: inline-block
                            vertical-align: middle
            .no-rating
                padding: 16px 0
                font-size: 12px
                color: rgb(147,153,159)
</style>



