<template>
    <div class="header">
        <div class="content-wrapper">
            <div class="avatar">
                <img width="64" height="64" v-bind:src="seller.avatar"/>
            </div>
            <div class="content">
                <div class="title">
                    <span class="brand"></span>
                    <span class="name">{{seller.name}}</span>
                </div>
                <div class="description">
                    {{seller.description}}/{{seller.deliveryTime}}分钟送达
                </div>
                <!--做兼容：动态加载数据的地方加一个v-if判断数据是否已经过来了-->
                <div v-if="seller.supports" class="support">
                    <span class="icon" :class="classMap[seller.supports[0].type]"></span>
                    <span class="text">{{seller.supports[0].description}}</span>
                </div>
            </div>
            <div v-if="seller.supports" class="support-count" @click="showDetail">
                <span class="count">{{seller.supports.length}}个&nbsp;&nbsp;></span>
            </div>
        </div>

        <div class="bulletin-wrapper" @click="showDetail">
            <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
        </div>

        <div class="background">
            <img :src = "seller.avatar" alt = "背景图" width="100%" height="100%" />
        </div>

        <div v-show="detailShow" class="detail">

        </div>
    </div>
</template>

<script>
    export default {
        props: {
            seller: {
                type: Object
            }
        },
        data() {
            return {
                detailShow: false
            }
        },
        methods: {
            showDetail() {
                this.detailShow = true
            }
        },
        created() {
          this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
        }
    }
</script>

<style lang="stylus" rel="stylesheet/stylus">
   @import "../../common/stylus/mixin.styl";

   .header
        position: relative
        overflow: hidden
        color: #fff
        background: rgba(7,17,27,0.5)
       .content-wrapper
           position: relative
           font-size: 0
           padding: 24px 12px 18px 24px
           .avatar
               display: inline-block
           .content
               display: inline-block
               margin-left:  16px
               font-size: 14px
               .title
                    margin: 2px 0 8px 0
                    .brand
                        display: inline-block
                        width: 30px
                        height: 18px
                        bg-image("brand")
                        background-size: 30px 18px
                        background-repeat: no-repeat
                    .name
                        margin-left: 6px
                        font-size: 16px
                        line-height: 18px
                        vertical-align: top
               .description
                    margin-bottom: 10px
                    line-height: 12px
                    font-size: 12px
               .support
                    .icon
                        display: inline-block
                        vertical-align: top
                        width: 12px
                        height: 12px
                        margin-right: 4px
                        background-size: 12px 12px
                        background-repeat: no-repeat
                        &.decrease
                            bg-image('decrease_1')
                        &.discount
                            bg-image('discount_1')
                        &.guarantee
                            bg-image('guarantee_1')
                        &.invoice
                            bg-image('invoice_1')
                        &.special
                            bg-image('special_1')
                    .text
                        font-size: 10px
                        line-height: 12px
           .support-count
               position: absolute
               right: 12px
               bottom: 18px
               padding: 0 8px
               height: 20px
               line-height: 20px
               border-radius: 14px
               background-color: rgba(0,0,0,0.2)
               text-align: center
               .count
                    vertical-align: top
                    font-size: 10px
               .icon-keyboard_arrow_right
                    font-size: 10px

       .bulletin-wrapper
            position:relative
            height: 28px
            line-height: 28px
            padding: 0 22px 0 12px
            white-space: nowrap
            overflow: hidden
            text-overflow:ellipsis
            background-color: rgba(7,17,27,0.2)
            .bulletin-title
                display: inline-block
                width:22px
                height: 12px
                bg-image('bulletin')
                vertical-align: top
                margin-top: 7px
                background-size: 22px 12px
                background-repeat: no-repeat
            .bulletin-text
                vertical-align: top
                margin: 0 4px
                font-size: 10px
                &:after
                    content:" >"
                    font-size: 10px
                    position: absolute
                    right: 10px
                    top: 0

       .background
           position: absolute
           top: 0
           left: 0
           width: 100%
           height: 100%
           z-index: -1
           filter: blur(10px)

       .detail
            position: fixed
            z-index: 100
            top: 0
            left: 0
            width: 100%
            height: 100%
            overflow: auto
            background: rgba(7,17,27,0.8)
</style>



